    mounted() {
        this.getData();
    },
    methods: {
        async getData(){
            let response = await fetch(`https://heartdroid.com:8443/release/${this.$route.params.name}`);
            response = (await response.json())[0];
            this.release = response;
            console.log(this.release.colors[0]);
        },
    }

}
